.button-container {
    display: flex;
    justify-content: flex-start; /* Aligns children to the left */
    align-items: center; /* Centers items vertically */
    flex-wrap: wrap; /* Allows items to wrap onto the next line */
    gap: 10px; /* Provides consistent spacing between child elements */
    padding: 10px; /* Adds padding around the entire container */
  }
  
  .button-style {
    background: transparent;
    border: 1px solid #ccc;
    padding: 5px 15px;
    border-radius: 4px;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
  }
  
  .divider {
    height: 0.25px;
    background-color: #ccc; /* Change color as needed */
    width: 100%; /* Takes full width of the container */
    margin: 10px 0; /* Adds vertical spacing */
  }

  .image-style {
    width: 150px;
    height: auto;
  }

  .card-style {
    width: 100%;
    height: 100%;
  }
  
  .subtitle-style {
    font-weight: 20; /* Lightweight font for the subtitle */
  }

  .titleStyle {
    font-size: 24px;
  }
  